import React, { useState } from "react";
import Radio from "@mui/material/Radio";
import { Button, TextField } from "@mui/material";
import RadioButtonGroup from "./RadioButtonGroup";
import Icon from "./Icon";
import card from "../images/card.svg";
import mars from "../images/mars-symbol.svg";

function SignUpForm() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    password: ""
  });
  const [selectedRadio, setSelectedRadio] = useState("Male");

  const handleRadioChange = (event) => {
    setSelectedRadio(event.target.value);
  };

  const handleChange = (event) => {
    setFormData({
      ...formData,
      [event.target.name]: event.target.value
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log(formData);
  };

  return (
    <div className="info">
      <form onSubmit={handleSubmit} className="info-fill">
        <div className="TextArea">
          <label htmlFor="name">Name</label>
          <TextField
            fullWidth
            id="demo-helper-text-misaligned"
            variant="outlined"
            placeholder="Letsie Ramaema"
            margin="normal"
            size="small"
            label="Name"
          />
        </div>
        <div className="TextArea">
          <label htmlFor="name">Customer ID</label>
          <RadioButtonGroup
            row
            fullWidth
            placeholder="Gender"
            options={["Male", "Female"]}
            value={selectedRadio}
            handleChange={handleRadioChange}
          />
        </div>
        <div className="TextArea">
          <label htmlFor="name">Gender</label>
          <TextField
            fullWidth
            id="demo-helper-text-misaligned"
            variant="outlined"
            placeholder="Letsie Ramaema"
            margin="normal"
            size="small"
          />
        </div>

        <div className="TextArea">
          <label htmlFor="date_of_birth">Date of birth</label>
          <TextField
            fullWidth
            id="demo-helper-text-misaligned"
            variant="outlined"
            margin="normal"
            size="small"
            placeholder="01/02/1983"
          />
        </div>

        <div className="TextArea">
          <label htmlFor="email">Email</label>
          <TextField
            fullWidth
            id="demo-helper-text-misaligned"
            variant="outlined"
            margin="normal"
            size="small"
            lplaceholder="ricco@gmail.com"
          />
        </div>
        <div className="TextArea">
          <label htmlFor="name">Mobile</label>
          <TextField
            fullWidth
            id="demo-helper-text-misaligned"
            variant="outlined"
            placeholder="+91 98965 43210"
            margin="normal"
            size="small"
          />
        </div>
        <div className="TextArea">
          <label htmlFor="name">Customer ID</label>
          <TextField
            fullWidth
            id="demo-helper-text-misaligned"
            variant="outlined"
            placeholder="576802-ERDO348 45"
            margin="normal"
            size="small"
          />
        </div>
        <div className="TextArea">
          <label htmlFor="name">Membership</label>
          <RadioButtonGroup
            row
            fullWidth
            label="Gender"
            options={["clasic", "Silver", "Gold"]}
            value={selectedRadio}
            handleChange={handleRadioChange}
            control=<Radio
              icon={<Icon src={card} alt="cardIcon" />}
              checkedIcon={<Icon src={card} alt="cardIcon" />}
            />
          />
        </div>
        <div className="buttons">
          <Button style={{ backgroundColor: "#f5f8f9" }} variant="contained">
            cancel
          </Button>
          <Button
            style={{
              textAlign: "right",
              marginRight: "0",
              marginLeft: "2em",
              backgroundColor: "#49c8a8"
            }}
            variant="contained"
          >
            save
          </Button>
        </div>
      </form>
    </div>
  );
}

export default SignUpForm;
