import React, { useState } from "react";
import face from "../images/face.svg";
import SignUpForm from "./SignUpForm";
import Description from "./Description";
import arrow from "../images/arrow.svg";

const Profile = () => {
  const [showComponent, setShowComponent] = useState(false);

  const toggleDisplay = () => {
    setShowComponent(!showComponent);
  };

  return (
    <div className="container">
      <div className="profile">
        <div className="profile-content">
          <img src={face} className="profile-img" alt="Profile" />
          <div className="descript">
            <h2>Front-end challenge!</h2>
            <p>This is a design that you will need to code up and imoress us</p>
          </div>
          <button className="toggleButton" onClick={toggleDisplay}>
            <img src={arrow} className="toggleIcon" alt="Toggle" />
          </button>
        </div>
      </div>
      <div>{showComponent ? <SignUpForm /> : <Description />}</div>
    </div>
  );
};

export default Profile;
